
import { defineComponent } from 'vue'
import { VueScrollPicker } from 'vue-scroll-picker'

export default defineComponent({
  emits: ['onHeightSelect'],
  components: {
    VueScrollPicker,
  },
  props: {
    isVisible: Boolean,
  },
  data() {
    return {
      currentValue: 140,
    }
  },
  computed: {
    options() {
      let res = []
      for (let i = 140; i < 156; i++) {
        res.push({ value: i, name: i })
      }
      return res
    },
  },
})
